import React, { useState } from 'react';
import { ResultCard } from './ResultCard';

export const NominateMovies = () => {
    const [searching, setSearching] = useState(false);
    const [message, setMessage] = useState(null);
    const [query, setQuery] = useState('');
    const [movies, setMovies] = useState([]);
    const searchMovies = async(e) =>{
        e.preventDefault();
        setSearching(true);
        const url = `https://www.omdbapi.com/?&apikey=c5b0e5f8&s=${query}&type="movie"`;
         try{
            const response = await fetch(url);
            const data = await response.json();
            setMessage(null);
            setMovies(data.Search);
            setSearching(false);
         }catch(err){
            setMessage('An unexpected error occured.')
            setSearching(false);
         }
    }
    return (

        <div className="add-page">
            <div className="container">
                <div className="add-content">
                    <div className="input-wrapper">
                       <form  onSubmit={searchMovies}>
                            <input type="text" name="query" placeholder="Search movies by name..."
                              value={query} onChange={(e) =>setQuery(e.target.value)}/>
               
                                 <button type="submit" className="btn">
                                   Search
                               </button>
                        </form>
                    </div>

                    {searching ?( <span> loading... </span>): (
                                    <ul className="results">
                                    {movies.map((movie, index) =>(
                                        <li key={index}>
                                            <ResultCard movie={movie}/>
                                        </li>
            
                                    ))}
                                </ul>
                    )}
                    {/* {searching = false && ( <h2>this is not a validated movie</h2>)} */}
                    
                
         
                </div>
            </div>
        </div>
 


         
        // <div className="add-page">
        //     <div className="container">
        //         <div className="add-content">
        //             <div className="input-wrapper">
        //                 <input
        //                    type="text"
        //                    placeholder="search for movie to nominate"
        //                    value={query}
        //                    onChange={moviechg}
        //                 />
        //             </div>
                    
        //                 <ul className="results">
        //                     {results.map((movie, index) =>(
        //                         <li key={index}>
        //                             <ResultCard movie={movie}/>
        //                         </li>
        //                     ))}
        //                 </ul>
                  
        //         </div>
        //     </div>
        // </div>
    )
};
